/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  color: #000000;
}

p {
  text-align: justify;
  font-size: 1.7rem;
  font-weight: 400;
}

.contenedor {
  width: 95%;
  max-width: 135rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 2.5rem 0;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-family: "Inter", sans-serif;
  font-size: 3rem;
  font-weight: 800;
  text-align: center;
}

h3 {
  font-size: 3rem;
  margin: 0;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.boton-blanco, .navegacion .boton-blanco {
  background-color: #FFFFFF;
  color: #231F57;
  font-weight: 600;
  padding: 1rem 2.5rem;
  text-align: center;
  margin-top: separacion/2;
  border-radius: 5rem;
  border: 0.1rem solid #231F57;
  font-size: 1.5rem;
}

.boton-azul, .boton-azul, .navegacion .boton-azul {
  background-color: #231F57;
  color: #FFFFFF;
  font-weight: 600;
  padding: 1rem 2.5rem;
  text-align: center;
  margin-top: separacion/2;
  border-radius: 5rem;
  border: 0.1rem solid #231F57;
  font-size: 1.5rem;
}

.boton-chatbot {
  background-color: #FFFFFF;
  color: #000000;
  font-weight: 600;
  padding: 1rem 2.5rem;
  text-align: center;
  margin-top: separacion/2;
  border-radius: 5rem;
  border: 0.1rem solid #231F57;
  font-size: 1.5rem;
}

.footer {
  width: 98%;
  max-width: 140rem;
  margin-right: auto;
  margin-left: auto;
}
.footer p {
  font-weight: 400;
  font-size: 1.4rem;
  text-align: center;
}

.contenedor-footer {
  width: 100%;
  background-color: #231F57;
  display: grid;
  grid-template-columns: 1.3fr 1fr 1.5fr 1fr;
  column-gap: 2rem;
  border-radius: 3rem;
  margin-bottom: 1rem;
}
.contenedor-footer h6 {
  font-family: "Inter", sans-serif;
  color: #30BDBC;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 900;
  margin-bottom: 2.5rem;
}
.contenedor-footer .navegacion-footer {
  display: flex;
  flex-direction: column;
}
.contenedor-footer .navegacion-footer a {
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: 500;
  margin-top: 1.3rem;
}
.contenedor-footer .navegacion-footer a:first-child {
  margin-top: 0;
}
.contenedor-footer ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  padding-left: 0;
}
.contenedor-footer li {
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: 500;
  margin-top: 1.3rem;
  display: flex;
  gap: 1rem;
}
.contenedor-footer li:first-child {
  margin-top: 0;
}
.contenedor-footer .footer-1 {
  background-color: #FFFFFF;
  margin: 3.5rem;
  border-radius: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1rem;
}
.contenedor-footer .footer-1 h5 {
  font-family: "Inter", sans-serif;
  color: #30BDBC;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 900;
  margin-bottom: 1rem;
  text-align: center;
  margin-top: 1.2rem;
  line-height: 2.5rem;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
}
.contenedor-footer .footer-2, .contenedor-footer .footer-3, .contenedor-footer .footer-4 {
  padding-left: 3rem;
}

.contenido-eslogan {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
}
.contenido-eslogan h1 {
  color: #231F57;
  font-weight: 800;
  text-transform: uppercase;
  font-size: 5.9rem;
  text-align: left;
  line-height: 1.1;
  max-width: 70rem;
  margin-left: 2rem;
}
.contenido-eslogan .eslogan {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  background-image: url("../../build/img/Vector1.svg");
  background-size: contain; /* Ajusta la imagen para cubrir todo el cuadrado */
  background-repeat: no-repeat;
  background-position: center;
  background-position-y: 45%; /* Mover un poco hacia arriba */
  background-position-x: 0%;
  background-size: 80%;
}
.contenido-eslogan .eslogan_img {
  width: 100%; /* Ancho del cuadrado */
  height: 54rem; /* Alto del cuadrado */
  background-image: url("../../build/img/imagen_seccion_eslogan.png"); /* Ruta de la imagen */
  background-size: cover; /* Ajusta la imagen para cubrir todo el cuadrado */
  background-position: center;
  border-radius: 4rem;
}

.barra {
  display: flex;
  justify-content: space-between;
  padding-top: 1rem;
}
.barra .lado-izq {
  display: flex;
}
.barra .lado-izq img {
  height: 6rem;
  width: 6rem;
}
.barra .lado-izq .titulo {
  max-width: 20rem;
  color: #231F57;
  text-decoration: none;
  line-height: 1.2;
  font-weight: 800;
  font-family: "Poppins", sans-serif;
  font-size: 1.8rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-left: 0.8rem;
}
.barra .lado-izq .titulo .segunda_parte {
  color: #30BDBC;
}

.navegacion {
  display: flex;
  align-items: center;
}
.navegacion a {
  color: #000000;
  font-size: 1.5rem;
  margin-right: 2rem;
  font-weight: 600;
}
.navegacion a:last-child {
  margin-right: 0;
}

.seccion-principal h2 {
  font-size: 3rem;
  font-weight: 800;
  text-align: center;
}
.seccion-principal .seccion {
  display: grid;
  grid-template-columns: 0.7fr 1fr;
  gap: 2rem;
}
.seccion-principal .seccion span {
  margin-top: 4rem;
  font-weight: 800;
}
.seccion-principal .seccion_img {
  display: flex;
  justify-content: center;
  align-items: center;
}
.seccion-principal .seccion_img img {
  width: 45%;
  border-radius: 2rem;
}

.testimoniales-contenedor-cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.4rem;
}
.testimoniales-contenedor-cards .testimoniales-card {
  background-color: #30BDBC;
  padding: 4rem;
  border-radius: 2.4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.testimoniales-contenedor-cards .testimoniales-card img {
  width: 5.6rem;
  height: 5.6rem;
}
.testimoniales-contenedor-cards .testimoniales-card strong {
  text-transform: uppercase;
}

.estadisticas {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}
.estadisticas .estadisticas-img {
  display: flex;
  justify-content: center;
  align-items: center;
}
.estadisticas .estadisticas-img img {
  width: 43.8rem;
  height: 40.3rem;
}
.estadisticas .estadisticas-informacion {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 5rem;
  padding: 5rem;
}
.estadisticas .estadisticas-informacion h3 {
  font-weight: 900;
  font-size: 3.5rem;
  color: #231F57;
}
.estadisticas .estadisticas-informacion p {
  font-weight: 500;
  margin: 0;
}

.contenedor-chatbot {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 2rem;
}
.contenedor-chatbot button {
  color: #000000;
  font-size: 1.5rem;
  font-weight: 700;
  padding: 1.5rem;
  box-shadow: 2px 2px 2px 3px rgba(0, 0, 0, 0.2);
}
.contenedor-chatbot button:hover {
  background-color: #231F57;
  color: #FFFFFF;
  transition: all 0.5s;
}